body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f5f5f5;
  flex-direction: column;
}


h1 {
  margin: 0;
  color: rgba(175, 47, 47, 0.15);
  font-weight: 100;
  font-size: 650%;
}

.App {
  padding: 2vh;
  background-color: #fff;
  font-size: 200%;
  border-radius: 1%;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
  width: 50vw;
}

@media screen and (max-width: 650px){
  .App {
    font-size: 150%;
    width: 80vw;  
  }
}


.todo-item {
    display: flex;
    padding: 1vh 0;
    font-size: 80%;
    border-bottom: 1px solid #f1f1f1;
    margin-bottom: 2px;
}

.todo-item > input {
    display: block;
    flex: 1 1;
    border-radius: 50%;
}

.todo-item > span {
    display: block;
    flex: 10 1;
}

.todo-item > button {
    display: block;
    opacity: 0;
    flex: 1 1;
    border: none;
    border-radius: 2%;
    font-weight: bold;
    cursor: pointer;
    transition-duration: 200ms;
}

.todo-item > button:hover {
    border-radius: 13%;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    transition-duration: 200ms;
}

.show-delete-button {
    opacity: 1.0 !important;
    transition-duration: 150ms;
}
form {
  display: flex;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 5px;
}

form > #addTodoCheck {
  font-size: 50%;
  flex: 1 1;
}

form > #toggle-all-todo {
    color: black;
    flex: 1 1;
    font-size: 100%;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    opacity: 0.3;
}

form > #toggle-all-todo:hover {
    transition-duration: 120ms;
    opacity: 0.6;
    cursor: pointer;
}

form > #addTodoInput {
  font-size: 70%;
  padding: 3%;
  width: 100%;
  border: none;
  flex: 11 1;
  background: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
  font-weight: lighter;
  color: #4d4d4d;
}

form > button {
  display: none;
}

.full-opacity {
    opacity: 1.0 !important;
    transition-duration: 120ms;
}
#footer-section {
    display: flex;
    font-size: 50%;
    margin-top: 2vh;
    padding: 1%;
    font-weight: lighter;
}
